.darkModeSwitcher {
  width: 100px;
  height: 30px;
  background: #333;
  position: absolute;
  margin: 0 0 3rem 1rem;
  bottom: 0;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5),
    0px 1px 0px rgba(255, 255, 255, 0.2);
}
.darkModeSwitcher:after {
  content: "";
  position: absolute;
  top: 14px;
  left: 14px;
  height: 2px;
  width: 72px;
  background: #111;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5),
    0px 1px 0px rgba(255, 255, 255, 0.2);
}
.darkModeSwitcher label {
  display: block;
  width: 22px;
  height: 22px;
  cursor: pointer;
  position: absolute;
  top: 4px;
  z-index: 1;
  left: 4px;
  background: #fcfff4;
  border-radius: 50px;
  transition: all 0.4s ease;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
  background: linear-gradient(to bottom, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
}
.darkModeSwitcher label:after {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  top: 6px;
  left: 6px;
  background: #333;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px black, 0px 1px 0px rgba(255, 255, 255, 0.9);
}
.darkModeSwitcher input[type="checkbox"] {
  visibility: hidden;
}
.darkModeSwitcher input[type="checkbox"]:checked + label {
  left: 74px;
}
.darkModeSwitcher input[type="checkbox"]:checked + label:after {
  background: #27ae60;
}

.menu {
  display: -ms-flexbox;
  width: 26px;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
}
.menu > span {
  height: 3px;
  border-radius: 3px;
  margin-bottom: 6px;
  display: block;
  transition: all 0.25s ease-in-out;
}
.menu > span:first-child {
  width: 13px;
}
.menu > span:nth-child(2) {
  width: 26px;
}
.menu > span:nth-child(3) {
  width: 18px;
}
.menu:hover > span:first-child {
  width: 26px;
}
.menu:hover > span:nth-child(2) {
  width: 18px;
}
.menu:hover > span:nth-child(3) {
  width: 13px;
}

